import { Form, Input, notification } from "antd";
import { useHistory } from "react-router-dom";
import { ROUTES } from "../../../routes";
import { formRules } from "../../../shared/formRules";
import { useEffect } from "react";

export const LoginForm = ({ reset }: { reset?: boolean }) => {
  const [form] = Form.useForm();
  const router = useHistory();

  useEffect(() => {
    if (reset) form.resetFields();
  });

  return (
    <div>
      <Form
        name="login-form"
        key="login-form"
        form={form}
        layout="vertical"
        className=""
        id="login-form"
        // labelCol={{ span: 10 }}
        scrollToFirstError
        onFinish={async (data) => {
          const dataToPost = form.getFieldsValue();
          const { email, password } = form.getFieldsValue();
          // console.log("data to login ", dataToPost);
          if (email === "admin" && password === "admin") {
            router.push(ROUTES.ADMIN_PAGE.DASHBOARD);
          } else {
            notification.error({
              message: "Echec de connexion",
              description: "Identifiant ou mot de passe incorrect",
            });
          }
        }}
      >
        <Form.Item
          label="Email"
          name="email"
          rules={[formRules.ruleRequired]}
          // className="col-12"
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="Mot de passe"
          name="password"
          rules={[formRules.passwordRule(true)]}
          // className="col-12"
        >
          <Input.Password />
        </Form.Item>
        <div className="text-end">
          <u className="text-primary">Mot de passe oublié ?</u>
        </div>
        <div className="mx-auto mt-4">
          <button className="btn btn-primary w-100 rounded-pill" type="submit">
            Se connecter
          </button>
        </div>
      </Form>
    </div>
  );
};
