import styled from "@emotion/styled";
import { IMAGES } from "../../../shared/images";
import { Avatar, Image, Modal, Space, Spin } from "antd";
import { AddUserComponent } from "../../admin/components/Utilisateur/AddUserComponent";
import { AppFooter } from "../../shared/Footer";
import { useState, Suspense } from "react";
import { LoginForm } from "../components/LoginForm";
import {
  FaChartBar,
  FaCheckCircle,
  FaHandHoldingMedical,
  FaShieldAlt,
} from "react-icons/fa";
import { BS_BLUE, GREEN_DARK, ORANGE_DARK } from "../../../shared/colors";
import { useHistory } from "react-router-dom";
import { ROUTES } from "../../../routes";
import { Navbar } from "../../shared/Navbar";

const LoginContainer = styled.div`
  .one-half {
    background: url(${IMAGES.doctorm1}) no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
  }

  .form-register {
    background-color: hsl(0, 0%, 96%);
  }
`;

export const LoginPage = () => {
  const [isLogin, setIsLogin] = useState(true);
  const [isRegistered, setIsRegistered] = useState(false);
  const [resetForm, setResetForm] = useState(false);
  const router = useHistory();

  const descriptionList = [
    {
      title: "Ouvert",
      description:
        "Il est ouvert à tous les praticiens exerçant dans les institutions sanitaires au Cameroun et désirant y enregistrer leurs patients pour des besoins d'évaluation  ultérieure",
      color: GREEN_DARK,
      icon: <FaHandHoldingMedical size={28} />,
    },
    {
      title: "Evolutif",
      description:
        "Le registre se veut à long terme, un outil essentiel d'observation, d'analyse des statistiques de santé concernant la traumatologie, de conseil et d'aide à la prise de décisions sanitaires structurelles pour le législateur dans le cadre de la mise en place des politiques de santé publique.",
      color: ORANGE_DARK,
      icon: <FaChartBar size={28} />,
    },
    {
      title: "Sécurisé",
      description:
        "Le fonctionnement de ce registre numérique respecte toutes les exigences de sécurité requises et fixées par la Loi N°2010-12 du 12 décembre 2010 relative à la cybersécurité et à la cybercriminalité au Cameroun.",
      color: BS_BLUE,
      icon: <FaShieldAlt size={32} />,
    },
  ];

  const DescriptionCard = ({ item }: { item: any }) => (
    <div
      className="row"
      style={{
        animation: "3s infinite alternate slidein",
      }}
    >
      <div className="col-md-1 my-auto">
        <Avatar
          size="large"
          style={{
            backgroundColor: BS_BLUE,
            // backgroundColor: item.color,
            verticalAlign: "middle",
          }}
        >
          {item.icon}
        </Avatar>
      </div>
      <div className="col-md-11">
        {/* <span className="mb-3">{item.title}</span> */}
        <div className="">{item.description}</div>
      </div>
    </div>
  );

  const ModalRegistered = () => (
    <Modal
      open={isRegistered}
      onCancel={() => {
        setResetForm(true);
        router.push(ROUTES.HOME_PAGE);
      }}
      footer={null}
      centered
      className="text-center"
      title="Demande d'adhésion enregistrée"
      closable={false}
    >
      <div className="mx-auto mb-3 col-md-3 mt-3">
        <FaCheckCircle color={BS_BLUE} size={72} />{" "}
      </div>
      <span className="fs-bold">
        Votre demande a bien été enregistrée et vous recevrez une notification à
        votre adresse email. Nous traitons votre demande et une réponse vous
        sera envoyé dans 3 ou 4 jours.
      </span>
      <div>
        <button
          className="btn btn-outline-primary rounded-pill mt-5"
          onClick={() => {
            setIsRegistered(false);
            router.push(ROUTES.HOME_PAGE);
          }}
        >
          Aller à la page d'accueil
        </button>
      </div>
    </Modal>
  );

  const ModalRegistration = () => (
    <Modal
      open={!isLogin}
      onCancel={() => {
        setResetForm(true);
        setIsLogin(true);
      }}
      footer={null}
      title=""
      width="55%"
    >
      <div>
        <div className="pt-0 pb-2">
          <h5 className="card-title text-center pb-0 fs-4 fs-bold">
            Demande d'adhésion
          </h5>
          <p className="text-center text-muted small mb-3">
            Entrer vos informations personnelles pour créer votre compte
          </p>
        </div>
        <AddUserComponent
          setIsRegistered={setIsRegistered}
          registration={true}
          closeModal={() => setIsLogin(true)}
        />
      </div>
    </Modal>
  );

  return (
    <LoginContainer>
      <main>
        <div className="container">
          <section className="section register min-vh-100 d-flex flex-column align-items-center justify-content-center py-4">
            <div className="container py-3 h-100">
              <Navbar homeLink={ROUTES.ADMIN_PAGE.DASHBOARD} forLogin={true} />
              <div className="row mb-3 mt-4">
                <div className="col-md-1 mx-3">
                  <Image
                    src={IMAGES.logo}
                    alt="logo"
                    className="img-fluid"
                    width={100}
                  />
                </div>
                <h1 className="col-md-10 mb-0 display-6 fw-bold ls-tight text-start pt-3">
                  <span className="text-primary w-100">
                    Registre Camerounais de Traumatologie
                  </span>
                </h1>
              </div>
              <div className="row d-flex">
                {/* START DESCRIPTION */}
                <div className="col-md-8 col-lg-7 col-xl-7 h-100">
                  <p>
                    Ce tout premier Registre de Traumatologie du Cameroun a pour
                    but de colliger les données des patients traumatisés de
                    l'appareil locomoteur dans une optique à la fois sanitaire
                    et scientifique. <br />
                  </p>
                  <Space direction="vertical" size="large" className="mb-5">
                    {descriptionList.map((item) => (
                      <DescriptionCard
                        key={"site-description-" + item.title}
                        item={item}
                      />
                    ))}
                  </Space>
                  <div className="row">
                    <span className="text-center mx-auto">
                      L'association Camerounaise pour l'Ostéosynthèse est
                      initiatrice et gestionnaire du Registre Camerounais de
                      Traumatologie.
                    </span>
                    <div className="row mt-3">
                      <span className="col-md-6">
                        {" "}
                        Date de mise en service : Janvier 2023{" "}
                      </span>
                      <span className="col-md-6">
                        {" "}
                        Contact :{" "}
                        <a href="mailto: contact.rct@ao-cameroun.org">
                          contact.rct@ao-cameroun.org
                        </a>
                      </span>
                    </div>
                  </div>
                </div>
                {/* END DESCRIPTION */}
                {/* START LOGIN FORM */}
                <div
                  className="col-md-3 col-lg-4 col-xl-4 offset-xl-1 p-1"
                  style={{
                    backgroundColor: "hsl(0, 0%, 96%)",
                    borderRadius: 45,
                    maxHeight: "fit-content",
                  }}
                >
                  <div className="row p-3">
                    <div className="mb-3 mt-1">
                      <h5 className="card-title pb-0 pt-0 fs-4 mx-2 text-lead">
                        Déjà client ?
                      </h5>
                    </div>
                    <LoginForm reset={resetForm} />
                    <div className="mb-auto">
                      <hr className="p-0 mt-4 w-100" />
                      <span className="fs-4 ">Nouveau client ?</span>
                      <div className="mx-auto mt-4">
                        <button
                          className="btn btn-outline-primary w-100 rounded-pill"
                          onClick={() => setIsLogin(false)}
                        >
                          Créer un compte
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                {/* end LOGIN FORM */}
              </div>
            </div>
          </section>
          <ModalRegistered />
          <ModalRegistration />
        </div>
      </main>

      <a
        href="/"
        className="back-to-top d-flex align-items-center justify-content-center"
      >
        <i className="bi bi-arrow-up-short"></i>
      </a>
      <AppFooter />
    </LoginContainer>
  );
};

export default LoginPage;
